import TextField from "@mui/material/TextField";

export const Contact = () => {
    const handleSubmit = (event) => {
      event.preventDefault();
      const message = {
        firstName: event.target.elements.fname.value,
        lastName: event.target.elements.lname.value,
        email: event.target.elements.email.value,
        phone: event.target.elements.phone.value,
        text: event.target.elements.message.value
      };
      console.log(message);
      // Do something with the message object
    };
  
    return (
      <section className="contact flex column">
        <h1>
          I would love to hear from you, <br /> Get in touch👋
        </h1>
        <section className="form-section flex column">
          <form className="details-sec column flex wrap" onSubmit={handleSubmit}>
            <div className="name-sec flex">
              <TextField
                color="warning"
                className="f-name-input"
                name="fname"
                label="First name"
                size="small"
              />
              <TextField
                className="l-name-input"
                color="warning"
                name="lname"
                label="Last name"
                size="small"
              />
            </div>
            <div className="email-sec">
              <TextField
                className="email-input"
                color="warning"
                name="email"
                label="Youre Email"
                size="small"
              />
            </div>
            <div className="phone-sec">
              <TextField
                type="number"
                className="phone-input"
                color="warning"
                name="phone"
                label="Youre phone"
                size="small"
              />
            </div>
            <div className="message-sec">
              <p>Type your message here</p>
              <textarea
                className="message-input"
                name="message"
                id=""
                cols="30"
                rows="10"
              ></textarea>
            </div>
          </form>
          <input value="Submit" type="submit" className="rounded-btn" onClick={handleSubmit}/>
        </section>
      </section>
    );
  };
  